import * as React from "react"
import { graphql } from "gatsby"
import { GatsbyImage, getImage } from "gatsby-plugin-image"
import {
  Heading,
  Subhead,
  Text,
  ButtonList,
} from "./ui"
import {
  featureWrapper,
  innerWrapper,
  textWrapper,
  imgWrapper,
  featureImg
} from "./feature.css"

export default function Feature(props) {
  return (
    <div className={`${featureWrapper} ${props.flip}`}>
      <div className={innerWrapper}>
        <div className={textWrapper}>
          {props.name && <Heading as="h2">{props.name}</Heading>}
          <Subhead as="h3">{props.title}</Subhead>
          {props.bio && (
            <Text variant="lead"
              dangerouslySetInnerHTML={{
                __html: props.bio,
              }}
            />
          )}
          {props.links && <ButtonList links={props.links} />}
        </div>
        <div className={imgWrapper}>
          {props.image && (
            <GatsbyImage
              alt={props.image.alt}
              image={getImage(props.image.gatsbyImageData)}
              className={featureImg}
            />
          )}
        </div>
      </div>
    </div>
  )
}

export const query = graphql`
  fragment HomepageFeatureContent on HomepageFeature {
    id
    name
    title
    bio
    links {
      id
      href
      text
    }
    image {
      id
      gatsbyImageData
      alt
    }
  }
`
