import * as React from "react"
import { graphql } from "gatsby"
import { GatsbyImage, getImage } from "gatsby-plugin-image"
import {
  Nudge,
  Container,
  Heading,
  Text,
  ButtonList,
  Kicker,
} from "./ui"
import {
  ctaWrapper,
} from "./cta.css"

export default function HomepageCta(props) {
  return (
    <Container className={ctaWrapper}>
      {props.heading && (
        <Heading center>
          {props.kicker && <Kicker center>{props.kicker}</Kicker>}
          {props.heading}
        </Heading>
      )}
      {props.text && (
        <Text as="p" center variant="lead">
          {props.text}
        </Text>
      )}
      <div className="cta-button">
        <ButtonList links={props.links} variant="center" primary />
      </div>
      {props.image && (
        <Nudge left={5} right={5} bottom={5}>
          <GatsbyImage
            alt={props.image.alt}
            image={getImage(props.image.gatsbyImageData)}
          />
        </Nudge>
      )}
    </Container>
  )
}

export const query = graphql`
  fragment HomepageCtaContent on HomepageCta {
    id
    kicker
    heading
    text
    image {
      alt
      id
      gatsbyImageData
    }
    links {
      id
      href
      text
    }
  }
`
