import { graphql } from "gatsby"
import { GatsbyImage, getImage } from "gatsby-plugin-image"
import * as React from "react"
import {
  ButtonList,
  Heading,
  Kicker,
  Subhead,
} from "./ui"
import {
  heroWrapper,
  flexForty,
  flexSixty,
  flexHeroImg,
} from "./hero.css"

export default function Hero(props) {

  return (
    <div
      className={`hero ${props.color[0]} ${props.alignment[0].toLowerCase()} ${heroWrapper}`}
    >
      <div className={flexSixty}>
        <div>
          {props.h1 && (
            <Heading as="h2">
              {props.kicker && <Kicker>{props.kicker}</Kicker>}
              {props.h1}
            </Heading>
          )}
          {props.subhead && (
            <Subhead as="h3">{props.subhead}</Subhead>
          )}
          {props.body && (
            <div
              dangerouslySetInnerHTML={{
                  __html: props.body,
                }}
            />
          )}
          <ButtonList links={props.links} primary />
        </div>
      </div>
      <div className={flexForty}>
        {props.image && (
          <GatsbyImage
            alt={props.image.alt}
            image={getImage(props.image.gatsbyImageData)}
            className={flexHeroImg}
          />
        )}
      </div>
    </div>
  )
}

export const query = graphql`
  fragment HomepageHeroContent on HomepageHero {
    id
    kicker
    h1: heading
    subhead
    color
    alignment
    body
    links {
      id
      href
      text
    }
    image {
      id
      gatsbyImageData
      alt
    }
  }
`
